html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Formula 1", sans-serif;
  font-size: 2.4rem;
  height: 100vh;
  background-color: #151513;
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
  display: flex;
  flex-flow: column;
  transition: background-image 1s ease;
}

img {
  max-width: 100%;
  width: 100%;
  height: auto;
  display: block;
}

.background {
  content: "";
  position: absolute;
  height: 100vh;
  width: 100vw;
  top: 0;
  left: 0;
  background-color: rgba(21, 21, 19, 0.6);
  z-index: -1;
}

.opacity0 {
  opacity: 0;
}

.opacity1 {
  opacity: 1;
}

.bg-0 {
  background-image: url("../img/0.jpg");
}

.bg-1 {
  background-image: url("../img/1.jpg");
}

.bg-2 {
  background-image: url("../img/2.jpg");
}

.bg-3 {
  background-image: url("../img/3.jpg");
}

.header {
  flex: 0 1 15rem;
  padding: 3rem 5rem;
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
}
.header .left {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.header .right {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.logo {
  width: 16rem;
  filter: drop-shadow(5px 5px 5px rgba(0, 0, 0, 0.25));
  transition: filter 0.2s ease;
}
.logo:hover {
  filter: drop-shadow(5px 5px 5px rgba(255, 255, 255, 0.1));
}

.nav-left a {
  color: #FFFFFF;
  text-decoration: none;
  margin-right: 3rem;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  opacity: 75%;
  transition: opacity 0.2s ease;
  transition: text-shadow 0.2s ease;
}
.nav-left a:hover {
  opacity: 100%;
  text-shadow: 5px 5px 10px rgba(255, 255, 255, 0.25);
}

.categories {
  grid-row: 1/2;
  grid-column: 1/4;
  display: flex;
  justify-content: flex-end;
  gap: 3rem;
}
.categories a {
  font-weight: bold;
  color: #FFFFFF;
  text-decoration: none;
  opacity: 60%;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  transition: opacity 0.2s ease;
  transition: text-shadow 0.2s ease;
}
.categories a.active {
  opacity: 100%;
}
.categories a:hover {
  opacity: 100%;
  text-shadow: 5px 5px 10px rgba(255, 255, 255, 0.25);
}

.nav-right {
  display: flex;
  align-items: center;
  gap: 3rem;
}
.nav-right a {
  color: #FFFFFF;
  text-decoration: none;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  opacity: 80%;
  transition: opacity 0.2s ease;
  transition: text-shadow 0.2s ease;
}
.nav-right a:hover {
  opacity: 100%;
  text-shadow: 5px 5px 10px rgba(255, 255, 255, 0.25);
}

.options {
  display: flex;
  gap: 3rem;
}
.options .search {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  font-size: 1.6rem;
  width: 4rem;
  height: 4rem;
  color: #FFFFFF;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  transition: background-color 0.4s ease;
  transition: color 0.6s ease;
}
.options .search:hover {
  background-color: rgba(255, 255, 255, 0.8);
  color: gray;
}
.options .menu {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #FFFFFF;
  font-size: 4rem;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}

.principal {
  width: 60%;
  padding: 0 5rem;
  flex: 1 1 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
}
.principal .tag {
  font-size: 1.4rem;
  background-color: #FF0000;
  color: #FFFFFF;
  padding: 1rem 3rem;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  cursor: pointer;
}
.principal .tag p {
  margin: 0;
}
.principal .title {
  color: #FFFFFF;
  font-size: 3rem;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  cursor: pointer;
  transition: text-shadow 0.2s ease;
}
.principal .title h1 {
  margin: 3rem 0;
}
.principal .title:hover {
  text-shadow: 5px 5px 10px rgba(255, 255, 255, 0.1);
}
.principal .more {
  color: #FFFFFF;
  display: flex;
  gap: 3rem;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  font-size: 2rem;
  text-transform: uppercase;
  cursor: pointer;
  transition: text-shadow 0.2s ease;
}
.principal .more p {
  margin: 0;
}
.principal .more:hover {
  text-shadow: 1px 1px 5px rgba(255, 255, 255, 0.25);
}

.up-next {
  padding: 0 5rem;
  flex: 0 1 auto;
}
.up-next .next {
  color: #FFFFFF;
  text-transform: uppercase;
  font-size: 2rem;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
}
.up-next .news {
  display: flex;
  gap: 1rem;
}

.new {
  border-top: 0.5rem solid transparent;
  background-color: rgba(21, 21, 19, 0.6);
  padding: 3rem;
  color: #FFFFFF;
  min-height: 15rem;
  max-height: 20rem;
  display: flex;
  gap: 2rem;
  flex-direction: column;
  cursor: pointer;
  transition: all 0.5s ease;
}
.new:hover p, .new:hover h2 {
  opacity: 1;
}
.new p, .new h2 {
  margin: 0;
  font-weight: normal;
  text-shadow: 5px 5px 10px rgba(0, 0, 0, 0.25);
  opacity: 0.6;
  transition: opacity 0.2s ease;
}
.new.active {
  border-top: 0.5rem solid #FF0000;
}
.new.active p, .new.active h2 {
  opacity: 1;
}
.new .row-1 {
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 1.2rem;
}
.new .title {
  font-size: 1.8rem;
}